import * as React from 'react';
import { DailyTableRow } from '../../../../api/api';
import { StyledTableRow } from '../../StyledTable';
import { TableCell } from './TableCell/TableCell';

interface Props {
  rowData: DailyTableRow;
  onChangeData: (cellName: string) => (value: string | null) => void;
  rowIndex: number;
}

export const EditableTableRow: React.FC<Props> = ({ rowData, onChangeData, rowIndex }) => {
  const total: string = (
    Number(rowData.cash) +
    Number(rowData.bort) -
    Number(rowData.gas) -
    Number(rowData.fuel) -
    Number(rowData.washing) -
    Number(rowData.avans) -
    Number(rowData.spendings)
  )
    .toFixed(2)
    .toString();

  return (
    <StyledTableRow data-rowindex={rowIndex}>
      {[
        { name: 'autoNumber', placeHolder: ' ', data: rowData.autoNumber },
        { name: 'name', placeHolder: '', data: rowData.name },
        { name: 'cash', placeHolder: '', data: rowData.cash },
        { name: 'bort', placeHolder: '', data: rowData.bort },
        { name: 'washing', placeHolder: '', data: rowData.washing },
        { name: 'gas', placeHolder: '', data: rowData.gas },
        { name: 'fuel', placeHolder: '', data: rowData.fuel },
        { name: 'spendings', placeHolder: '', data: rowData.spendings },
        { name: 'avans', placeHolder: '', data: rowData.avans },
        { name: 'total', placeHolder: '', data: total, disabled: true },
      ].map((item, index) => {
        return (
          <TableCell
            key={rowData._id + item.name}
            onChangeData={onChangeData}
            cellName={item.name}
            placeHolder={item.placeHolder}
            value={item.data}
            cellIndex={index}
            rowIndex={rowIndex}
            rowId={rowData._id}
            disabled={item.disabled || false}
          />
        );
      })}
    </StyledTableRow>
  );
};
